import React, { useState, useEffect } from 'react';
import Button from '@mui/material/Button';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Box from '@mui/material/Box';
import Papa from "papaparse";
import { v4 as uuidv4 } from 'uuid';

const FileLoader = (props) => {

  const [isSelected, setIsSelected] = useState(false);
  const [selectedFile, setSelectedFile] = useState(undefined);
  const [csvParsed, setCSVParsed] = useState(false);
  const [csvErrors, setCSVErrors] = useState(false);
  const [columnCount, setColumnCount] = useState(0);
  const [columnFields, setColumnFields] = useState(undefined);
  const [csvData, setCSVData] = useState(null)
  const [sendCompleted, setSendCompleted] = useState(false)
  const [checkCompleted, setCheckCompleted] = useState(false)
  const [error, setError] = useState(false)

  useEffect(() => {

    if(selectedFile){
      Papa.parse(selectedFile, {
          header: true,
          worker: true,
          skipEmptyLines: true,
          complete: (result) => {

            if (result.errors.length > 0) {

                setCSVParsed(false);
                setCSVErrors(true);
                setIsSelected(false);

              } else {
                setCSVParsed(true);
                setCSVErrors(false);
                setColumnCount(result.meta.fields.length)  
                setColumnFields(result.meta.fields) 
                //props.updateDataCallback([], [])
                for(let i = 0; i < result.data.length; i++){
                  result.data[i]["UniqueID"] = uuidv4()
                }
                //if(props.sendResultsArray.length === 0){
                  let arr = []
                  let obj = {}
                  for(let i = 0; i < result.data.length; i++){
                    obj["sid"] = ""
                    obj["error"] = {}
                    obj["error"]["errorCode"] = ""
                    obj["error"]["errorMessage"] = ""
                    obj["error"]["errorLink"] = ""
                    obj["status"] = ""
                    obj["UniqueID"] = result.data[i].UniqueID
                    arr.push(obj)
                    obj = {}
                  }
                  props.updateDataCallback(null, arr)
                //}
                setCSVData(result.data)   
                
              }

          },
      });
      
    }
 
  }, [selectedFile])

  useEffect(() => {

    if(csvData){
      props.updateDataCallback(csvData)
      props.handleFileLoadingCallback(csvParsed, sendCompleted, checkCompleted, error, columnFields)
    }

  },[csvData])

  function onFileChange(event) {
    if (event.target.files.length > 0) {
      setIsSelected(true)
      setSelectedFile(event.target.files[0])
      setSendCompleted(false)
      setCheckCompleted(false)
      setError(false)
    }
  }


  return(
    <>
    <FormControl component="fieldset" margin="normal">
      <Button variant="contained" margin="normal" component="label" disabled={!props.isAuthenticated}>
        {isSelected ? selectedFile.name : "Upload file"}{" "}
        <input
          type="file"
          name="myFile"
          accept=".csv"
          onChange={onFileChange}
          hidden
        />
      </Button>
      </FormControl>

      {csvParsed && csvData ? (
        <Box mt={1}>
          <FormLabel>
            <b>Total Rows Count: </b>{props.csvData ? props.csvData.length: csvData.length}<br/>
            <b>Total Columns Count:</b> {columnCount}
          </FormLabel>{" "}
      </Box>
    ) : (
      ""
    )}

    {csvErrors ? (
      <Box mt={1}>
        <FormLabel>
          Errors parsing the CSV file <b>{selectedFile.name}</b>.{" "}
        </FormLabel>{" "}
      </Box>
    ) : (
      ""
    )}
    </>
  )
}

export default FileLoader;