import React, { useState, useEffect } from 'react';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import CircularProgress from '@mui/material/CircularProgress';
import DoneOutlineIcon from '@mui/icons-material/DoneOutline';
import CloseIcon from '@mui/icons-material/Close';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';

import '../config';

const Authentication = (props) => {


	const [username, setUsername] = useState("")
	const [password, setPassword] = useState("")
	const [submitting, setSubmitting] = useState(false)
	const [showLoadingIcon, setLoadingIcon] = useState(false)
	const [authSuccess, setAuthSuccess] = useState(false)
	const [accessToken, setAccessToken] = useState(null)
	const [firstTimeCheck, setFirstTimeCheck] = useState(true)

	function handleChangeUsername(event) {
		setUsername(event.target.value)
	}

	function handleChangePassword(event) {
		setPassword(event.target.value)
	}

	function handleSuccess(success){
		setAuthSuccess(success)
	}

	function authenticate() {
		
		setSubmitting(true)
		setLoadingIcon(true)

		const data = {
	      'username': username,
	      'password': password
	    }


	    fetch(`${global.config.url}jwt`, {
	      method: "POST",
	      headers: {
	        'Content-Type': 'application/json'
	      },
	      body: JSON.stringify(data),
	    })
      .then((res) => {
      	return res.json()
      })
      .then((data) => {
       	setSubmitting(false)
				setLoadingIcon(false)
				setFirstTimeCheck(false)
				if(data.success){handleSuccess(true); props.handleAuthSuccessCallback(data.token)}
      })
      .catch((error) => {
        console.log(error);
        setSubmitting(false)
				setLoadingIcon(false)
				setFirstTimeCheck(false)
      });

	}

	return(
		<>
				{authSuccess ? "" :
	        (<><TextField
	          id="username"
	          name="username"
	          value={username}
	          onChange={handleChangeUsername}
	          margin="normal"
	          required
	          placeholder="Username"
	         />
	         <TextField
	          id="password"
	          name="password"
	          value={password}
	          onChange={handleChangePassword}
	          margin="normal"
	          required
	          type="password"
	          placeholder="Password"
	         />
	         <Button 
	         variant="contained" 
	         margin="normal" 
	         component="label" 
	         disabled={!username || !password || authSuccess}
	         onClick={authenticate}
	         >
	         	Authenticate
	         </Button></>)
       		}
         {
         	showLoadingIcon ?
         	(<>
              <CircularProgress size={15}/>
              <Typography variant="h6"gutterBottom>
                Authenticating...
              </Typography>

            </>)
         	: ""
         }
         {
         	authSuccess && !firstTimeCheck ?
         	(
    
         		<Stack direction="row" alignItems="baseline" justifyContent="center" gap={1}>
	         		<DoneOutlineIcon style={{color: "green", fontSize: "small"}}/>
	         		<Typography variant="h6" gutterBottom style={{color: "green"}}>
	         		Authentication Successful
	         		</Typography>
         		</Stack>
        
         	): ""
         }
         {
         	!authSuccess && !firstTimeCheck ?
         	
         	(
         		<Box mt={1}>
				<Stack direction="row" alignItems="baseline" justifyContent="center" gap={1}>
	         		<CloseIcon style={{color: "red", fontSize: "small"}}/>
	         		<Typography variant="h6" gutterBottom style={{color: "red"}}>
	         		Authentication Failed
	         		</Typography>
	         	</Stack>
	         	</Box>
         	): ""
         }



	</>)


}

export default Authentication;