import React, { useState, useEffect } from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import RadioGroup from '@mui/material/RadioGroup';
import Radio from '@mui/material/Radio';
import FormControlLabel from '@mui/material/FormControlLabel';

const ChannelSelection = (props) => {

	const [radioSelection, setRadioSelection] = useState("SMS")
	
	function onRadioChange() {
		setRadioSelection(radioSelection === "SMS" ? "Whatsapp" : "SMS")
	}

	useEffect(() => {


		props.updateChannelSelectionCallback(radioSelection)

	}, [radioSelection])

	return (

		<Box mt={2}>
          <Typography variant="h5" component="h1" gutterBottom>
            Channel Type
          </Typography>
          <RadioGroup aria-label="channel" name="channel" value={radioSelection} onChange={onRadioChange}>
            <FormControlLabel value="SMS" control={<Radio disabled={!props.isAuthenticated}/>} label="SMS" />
            <FormControlLabel value="Whatsapp" control={<Radio disabled={!props.isAuthenticated} />} label="Whatsapp" />
          </RadioGroup>
        </Box>

	)

}

export default ChannelSelection