import React, { useState, useEffect } from 'react';
import Box from "@mui/material/Box";
import Checkbox from "@mui/material/Checkbox";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormControl from "@mui/material/FormControl";
import FormGroup from "@mui/material/FormGroup";
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import IconButton from '@mui/material/IconButton';
import CircularProgress from '@mui/material/CircularProgress';
import Typography from '@mui/material/Typography';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';
import '../config';

const WhatsappOptions = (props) => {

    const [templates_array, setTemplatesArray] = useState([]);
    const [selectedTemplate, setSelectedTemplate] = useState("")
    const [selectedTemplateID, setSelectedTemplateID] = useState("")
    const [showLoadingIcon, setLoadingIcon] = useState(false)
    const [loadTemplatesChecked, setLoadTemplates] = useState(false)
    const [stateUpdateValues, setStateUpdateValues] = useState({submitting: false, getTemplatesCompleted: false, error: false, showTemplatesLoadingIcon: false})

    function loadTemplatesChange() {
        setLoadTemplates(loadTemplatesChecked ? false: true)
    }

    function handleSelectedTemplate(event,child) {
        setSelectedTemplate(event.target.value)
        setSelectedTemplateID(child.props.id)
    }

    useEffect(() => {

        if(selectedTemplateID)
            props.updateMsgCallback(templates_array[selectedTemplateID].content)

    }, [selectedTemplateID])

    useEffect(() => {
        if(loadTemplatesChecked) {
            setLoadingIcon(true)
            setStateUpdateValues({ submitting: true, getTemplatesCompleted: false, error: false, showTemplatesLoadingIcon: true });
            fetch(`${global.config.url}get-templates`, {
                  method: "POST",
                  headers: {
                    'Content-Type': 'application/json',
                    'Authorization': props.auth_header
                }
                })
              .then((res) => res.json())
              .then((data) => {

                setStateUpdateValues({
                  submitting: false,
                  showLoadingIcon: false,
                  getTemplatesCompleted: true
                });
                setTemplatesArray(data.data.templates_array)
                setLoadingIcon(false)
              })
              .catch((error) => {
                console.log(error);
                setStateUpdateValues({
                  submitting: false,
                  showLoadingIcon: false,
                  getTemplatesCompleted: true
                });
              });

          } else{
            setTemplatesArray([])
            setSelectedTemplate("")
            setLoadingIcon(false)
            setStateUpdateValues({getTemplatesCompleted: false})
            props.updateMsgCallback("")
          }
    },[loadTemplatesChecked])

    return(
    <>
    {props.radioSelection === "Whatsapp" ?

    (
        <FormGroup>
            <Box>
            <FormControlLabel 
                control={<Checkbox 
                checked={loadTemplatesChecked}
                onChange={loadTemplatesChange} />} 
                label="Load Whatsapp Templates"/>
                {
                    showLoadingIcon ?

                    (<>
                      <CircularProgress size={15}/>
                      <Typography variant="h6"gutterBottom>
                        Loading Templates...
                      </Typography>

                    </>) : ""
                }
              {templates_array.length > 0 ?
                (
                  <FormControl fullWidth component="fieldset" margin="normal">
                  <InputLabel id="whatsapp-template-select-label">Select Whatsapp Template</InputLabel>
                  <Select labelId="whatsapp-template-select-label"
                  value={selectedTemplate}
                  onChange={(event,child) => {handleSelectedTemplate(event,child)}}
                  >
                  {templates_array.map((template,index) =>
                    <MenuItem key={index} id={index} value={template.sid}><b>Name</b>: {template.name}<br/><b>Content</b>: {template.content}<br/><b>Language</b>: {template.language}</MenuItem>
                  )}
                  </Select>
                  </FormControl>
                )
                :
                ""
              }
            </Box>
        </FormGroup>
    )
    :
    ""
    }
    </>
    )
}

export default WhatsappOptions;